.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  background-color: rgba(0, 0, 0, 0.6);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  -ms-overflow-style: none;
  scrollbar-width: none; }
  .modal::-webkit-scrollbar {
    display: none; }
  .modal__wrapper {
    position: relative;
    display: flex;
    flex-shrink: 0;
    justify-content: center;
    align-items: center;
    width: 100%;
    min-height: 100%;
    padding: 60px 40px; }
    @media (max-width: 767px) {
      .modal__wrapper {
        padding: 60px 16px; } }
  .modal__overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    cursor: pointer; }
  .modal__content {
    position: relative;
    width: 600px;
    padding: 40px;
    background-color: #ffffff; }
    @media (max-width: 767px) {
      .modal__content {
        width: 100%;
        padding: 20px; } }
  .modal__close-btn {
    position: absolute;
    top: 15px;
    right: 15px; }
  .modal--responsive .modal__content {
    width: 100%; }
  .modal--fit-content .modal__content {
    width: auto; }
    @media (max-width: 767px) {
      .modal--fit-content .modal__content {
        width: 100%; } }
  .modal--no-scale {
    transform: none; }
  .modal--preload {
    display: none; }
  .modal--active {
    transform: scale(1);
    visibility: visible;
    opacity: 1;
    pointer-events: auto; }

